




































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import { ElForm } from 'element-ui/types/form';

@Component
export default class Position extends Vue {
    @Prop() public data!: any;

    public form: any = {
        coordx: 0,
        coordy: 0,
        zone: '0'
    };

    public zones: string[][] = [];

    public created() {
        this.form.coordx = this.data.coordx;
        this.form.coordy = this.data.coordy;
        this.form.zone = this.data.zone;

        this.$http
            .get('/api/zone/getall', {
                currentPage: 1,
                pageSize: 10000
            })
            .then((res: ResponseData) => {
                this.zones = res.pagedData.datas.map((v: any) => [
                    v.id + '',
                    v.name + (v.name.endsWith('区域') ? '' : '区域')
                ]);
                if (this.zones.every(v => v[0] !== this.form.zone)) {
                    this.form.zone = this.zones[0][0];
                }
            })
            .catch(console.error);
    }

    public onSubmit() {
        const form = <ElForm>this.$refs.form;
        form.validate((valid: boolean) => {
            if (valid) {
                // TODO 提交数据
                const data = Object.assign({}, this.form, {
                    updateUser: '....',
                    updateTime: Date.now(),
                    id: this.data.id
                });

                this.$confirm('确认修改')
                    .then(() =>
                        this.$http.post('/api/base/updateBase', data, {
                            'Content-Type': 'application/json'
                        })
                    )
                    .then(() => this.$message.success('修改成功'))
                    .catch(console.log);
            }
        });
    }
}
