





































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import IpInput from '../../components/IpInput.vue';
import { ElForm } from 'element-ui/types/form';

@Component({
    components: {
        'ip-input': IpInput
    }
})
export default class Net extends Vue {
    @Prop() public data!: IBaseStation;

    public form: any = {
        baseIp: [],
        serverIp: [],
        mask: [],
        mac: [],
        mode: 85
    };

    public onSubmit() {
        if (this.isValid !== true) {
            return;
        }

        const form = <ElForm>this.$refs.form;
        form.validate()
            .then(() => this.$confirm('确认设置?'))
            .then(() =>
                this.$http.post('/api/protocol/sendProtocol', {
                    ip: this.data.ip,
                    port: 50000,
                    protocol: '42' + this.parse(this.form)
                })
            )
            .then(() => this.$message.success('设置成功'))
            .catch(console.log);
    }

    private get isValid() {
        if (this.form.baseIp.length !== 4) {
            return this.$message.error('基站IP格式错误');
        }
        if (this.form.mask.length !== 4) {
            return this.$message.error('基站mask格式错误');
        }
        if (this.form.mac.length !== 6) {
            return this.$message.error('基站mac格式错误');
        }
        if (this.form.serverIp.length !== 4) {
            return this.$message.error('服务器IP格式错误');
        }

        return true;
    }

    // 解析基站网络设置
    private parse(value: string | IJson) {
        // [[key, byte, 是否用数组表示]]
        const keys = [
            ['baseIp', 4, true],
            ['mask', 4, true],
            ['mac', 6, true],
            ['basePort', 2, false],
            ['serverIp', 4, true],
            ['serverPort', 2, false],
            ['mode', 1, false]
        ];

        if (typeof value === 'string') {
            return this.str2obj(keys, value);
        }

        return this.obj2str(keys, value);
    }

    // 配置命令串转显示用对象
    private str2obj(keys: any[], value: string) {
        const parse210 = (v: string) => +('0x' + v);
        const parseToArr = (s: number, l: number) => {
            const arr = value.substr(s, l).match(/\d{2}/g) || [];

            return arr.map(parse210);
        };

        const data: IJson = {};
        let start: number = 0;
        keys.forEach(([k, byte, isArr]) => {
            const len = <number>byte * 2;

            data[<string>k] = isArr
                ? parseToArr(start, len)
                : parse210(value.substr(start, len));

            start += len;
        });

        return data;
    }

    // 显示对象转配置命令串
    private obj2str(keys: any[], value: IJson) {
        return keys
            .reduce((a, [k, byte]) => {
                let str: number | string | number[] = value[k];
                if (Array.isArray(str)) {
                    str = str
                        .map(v => v.toString(16).padStart(2, '0'))
                        .join('');
                } else {
                    str = (+str).toString(16).padStart(byte * 2, '0');
                }

                a.push(str);

                return a;
            }, [])
            .join('');
    }
}
